@import url(./vendor/swiper.min.css);
.site-container {
  overflow: hidden;
  min-width: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}

@media (max-width: 1200px) {
  .container {
    max-width: 970px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 750px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: none;
  }
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-family: "Montserrat", sans-serif !important;
  font-size: 16px;
  font-weight: 300;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "Montserrat", sans-serif !important;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif !important;
}

.header {
  position: fixed;
  left: 0;
  z-index: 50;
  padding: 20px 0;
  width: 100%;
  transition: all 0.3s ease 0s;
}

@media (max-width: 992px) {
  .header {
    padding: 20px 0;
  }
}

@media (max-width: 776px) {
  .header {
    padding: 10px 0;
  }
}

.header_scroll {
  background: rgba(85, 85, 85, 0.685);
}

.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 992px) {
  .header__logo {
    position: relative;
    z-index: 3;
  }
}

.header__menu {
  margin-left: 14%;
}

@media (max-width: 1200px) {
  .header__menu {
    margin: 0;
  }
}

@media (max-width: 992px) {
  .header__menu {
    padding: 120px;
  }
}

.header__search {
  margin-left: 28px;
}

.logo {
  flex: 0 0 181px;
}

.logo__img {
  max-width: 100%;
  object-fit: cover;
}

@media (max-width: 776px) {
  .logo {
    flex: 0 0 120px;
  }
}

@media (max-width: 992px) {
  .menu {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.836);
    transform: translateY(-100%);
  }
  .menu_active {
    transform: translateY(0);
    transition: transform 0.8s ease 0s;
  }
}

.menu__list {
  display: flex;
}

@media (max-width: 992px) {
  .menu__list {
    display: block;
  }
}

.menu__item:not(:last-child) {
  margin-right: 28px;
}

@media (max-width: 992px) {
  .menu__item:not(:last-child) {
    margin: 0 auto 30px;
    font-size: 20px;
  }
}

.menu__link {
  font-size: 14px;
  color: #fff;
  transition: 0.5s all;
}

.menu__link:hover {
  color: #c7b299;
}

.actions-item {
  display: flex;
  position: relative;
}

.actions-item__img {
  width: 19px;
  height: 25px;
  line-height: 24px;
  fill: #fff;
  transition: all 0.5s ease 0s;
  cursor: pointer;
}

.actions-item__img:hover {
  fill: #c7b299;
}

.actions-item__img:not(:last-child) {
  margin-right: 30px;
}

.actions-item__input {
  position: absolute;
  right: 30px;
  bottom: -40px;
  padding: 3px;
  width: 0;
  height: 30px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s ease 0s;
  -webkit-box-shadow: 5px 5px 15px 5px #000000;
  box-shadow: 5px 5px 15px 5px #000000;
}

.show-input {
  width: 150%;
  opacity: 1;
  visibility: visible;
}

.hidden {
  overflow: hidden;
}

.intro {
  position: relative;
}

.intro__button-next, .intro__button-prev {
  top: 49%;
  height: 73px;
  color: #fff;
}

@media (max-width: 776px) {
  .intro__button-next, .intro__button-prev {
    display: none;
  }
}

.intro__button-next:after, .intro__button-prev:after {
  font-size: 72px;
}

@media (max-width: 868px) {
  .intro__button-next:after, .intro__button-prev:after {
    font-size: 50px;
  }
}

.intro__button-next:hover, .intro__button-prev:hover {
  color: #d3c1ad;
}

.intro__slide {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vh;
  height: 100vh;
}

.intro__slide:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  background: rgba(0, 0, 0, 0.507);
}

.intro__slide-content {
  content: "";
  position: absolute;
  z-index: 2;
  text-align: center;
}

.intro__slide-subtitle {
  margin-bottom: 11px;
  font-family: "Raleway", sans-serif !important;
  font-weight: 500;
  font-size: 36px;
  color: #fff;
}

@media (max-width: 868px) {
  .intro__slide-subtitle {
    font-size: 20px;
  }
}

.intro__slide-title {
  margin-bottom: 19px;
}

.intro__slide-title h1 {
  font-family: "Raleway", sans-serif !important;
  font-weight: 800;
  font-size: 52px;
  letter-spacing: 0.5px;
  color: #fff;
}

@media (max-width: 868px) {
  .intro__slide-title h1 {
    font-size: 40px;
  }
}

.intro__slide-descr {
  margin: 0 auto 41px;
  max-width: 562px;
}

@media (max-width: 868px) {
  .intro__slide-descr {
    font-size: 12px;
  }
}

.intro__slide-descr p {
  line-height: 1.5;
  color: #fff;
}

.btn {
  display: inline-block;
  border-bottom: 4px solid #736357;
  border-radius: 3px;
  padding: 0 38px;
  font-weight: 800;
  font-size: 14px;
  line-height: 54px;
  text-transform: uppercase;
  color: #fff;
  background-color: #998675;
  cursor: pointer;
}

@media (max-width: 992px) {
  .btn {
    line-height: 45px;
    padding: 0 20px;
    font-size: 10px;
  }
}

.swiper-slide__bg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.intro__button-prev {
  left: 8%;
}

.intro__button-next {
  right: 8%;
}

.swiper-pagination {
  padding-bottom: 30px;
}

.swiper-pagination-bullet {
  width: 11px;
  height: 11px;
  background-color: #fff;
  opacity: 0.5;
}

.swiper-pagination-bullet-active {
  border: 1px solid #d3c1ad;
  background: transparent;
  opacity: 1;
  transform: scale(1.5);
}

.skills {
  padding: 80px 0;
}

@media (max-width: 992px) {
  .skills {
    padding: 40px;
  }
}

.skills__row {
  display: flex;
  margin: 0 -15px;
}

@media (max-width: 992px) {
  .skills__row {
    flex-wrap: wrap;
    margin: 0;
  }
}

.skills__column {
  position: relative;
  padding: 0 15px;
  text-align: center;
  cursor: pointer;
}

@media (max-width: 992px) {
  .skills__column {
    margin: 0 auto;
    padding: 0;
    max-width: 400px;
  }
  .skills__column:not(:last-child) {
    margin-bottom: 15px;
  }
}

.skills__column:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 70px;
  height: 2px;
  background: #f1eee9;
  transform: translate(-50%);
  transition: all 0.8s ease 0s;
}

.skills__column_active .skills__img {
  fill: #c7b299;
}

.skills__column:hover .skills__img {
  fill: #c7b299;
}

.skills__column_active:after, .skills__column:hover:after {
  background: #c7b299;
}

.skills__image {
  margin: 0 auto 28px;
  width: 49px;
  height: 49px;
  transform: scale(0);
  transition: all 0.8s ease 0s;
}

.skills__image._anim-active {
  transform: scale(1);
}

.skills__title {
  margin-bottom: 15px;
}

.skills__title h2 {
  font-weight: 600;
  font-size: 22px;
  color: #555;
}

.skills__text {
  margin-bottom: 30px;
}

.skills__text p {
  font-size: 14px;
  line-height: 1.71;
}

.skills__img {
  width: 100%;
  height: 100%;
  fill: #555;
}

.browsers {
  padding-top: 80px;
  background: #fbfaf8;
}

@media (max-width: 992px) {
  .browsers {
    padding-top: 40px;
  }
}

@media (min-width: 1200px) {
  .browsers__carousel {
    overflow: visible;
  }
}

.browsers__title h3 {
  color: #555;
}

@media (max-width: 767px) {
  .browsers__descr {
    font-size: 12px;
  }
}

.browsers__descr p {
  color: #8c8c8c;
}

.browsers__footer {
  display: flex;
  justify-content: space-around;
  padding: 41px 0;
  background: linear-gradient(to right, #211b19, #4e3427);
}

@media (max-width: 776px) {
  .browsers__footer {
    display: block;
    padding: 20px 0;
    text-align: center;
  }
}

.browsers__footer-subtitle {
  font-size: 18px;
  color: #615855;
}

@media (max-width: 549px) {
  .browsers__footer-subtitle {
    margin-bottom: 10px;
  }
}

.browsers__footer-row {
  margin-bottom: 10px;
}

.browsers__footer-title {
  margin-bottom: 8px;
}

.browsers__footer-title h4 {
  font-family: "Raleway", sans-serif !important;
  font-size: 28px;
  color: #fff;
}

@media (max-width: 992px) {
  .browsers__footer-title h4 {
    font-size: 20px;
  }
}

.browsers__footer-title h4 span {
  color: #c7b299;
}

.browsers__footer-btn {
  margin-left: -233px;
  padding: 0 27px;
}

@media (max-width: 776px) {
  .browsers__footer-btn {
    margin: 0;
  }
}

@media (max-width: 1200px) {
  .browsers__slider {
    display: flex;
    flex-direction: row;
  }
}

.titles__body {
  margin: 0 auto;
  max-width: 757px;
  text-align: center;
}

.titles__title {
  margin-bottom: 16px;
}

.titles__title h3 {
  font-family: "Raleway", sans-serif !important;
  font-weight: 600;
  font-size: 30px;
}

@media (max-width: 767px) {
  .titles__title h3 {
    font-size: 20px;
  }
}

.titles__descr {
  margin-bottom: 40px;
}

@media (max-width: 992px) {
  .titles__descr {
    margin-bottom: 20px;
  }
}

.titles__descr p {
  line-height: 1.75;
}

@media (min-width: 1200px) {
  .carousel__wrapper {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    height: 482px;
  }
}

.carousel__item {
  height: 400px;
  width: 770px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

@media (max-width: 1200px) {
  .carousel__item {
    height: 100%;
  }
}

@media (min-width: 1200px) {
  .carousel__item {
    content: "";
    position: absolute;
    opacity: 0.8;
    transition: all 1.5s ease 0s;
  }
  .carousel__item:hover {
    z-index: 3;
    opacity: 1;
  }
  .carousel__item:nth-child(2) {
    z-index: 2;
    height: 482px;
    opacity: 1;
  }
  .carousel__item:first-child {
    left: -200%;
  }
  .carousel__item:nth-child(2) {
    transform: translateY(50%);
    opacity: 0;
  }
  .carousel__item:last-child {
    right: -200%;
  }
}

.carousel__item._anim-active:first-child {
  left: -30px;
}

.carousel__item._anim-active:nth-child(2) {
  transform: translateY(0);
  opacity: 1;
}

.carousel__item._anim-active:last-child {
  right: -30px;
}

.carousel__img {
  max-width: 100%;
  object-fit: cover;
}

@media (min-width: 1200px) {
  .carousel__img {
    height: 100%;
  }
}

.carousel__pagination {
  display: none;
}

@media (max-width: 1200px) {
  .carousel__pagination {
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;
    margin-left: 20px;
    padding: 0;
  }
  .carousel__pagination .swiper-pagination-bullet {
    border-radius: 10%;
    width: 20px;
    height: 20px;
    background: rgba(122, 122, 122, 0.541);
  }
  .carousel__pagination .swiper-pagination-bullet:not(:last-child) {
    margin-bottom: 20px;
  }
}

.potrfolio__inner {
  padding: 76px 0 77px;
  text-align: center;
}

@media (max-width: 776px) {
  .potrfolio__inner {
    padding: 30px 0;
  }
}

.potrfolio__breadcrumbs {
  margin-bottom: 40px;
}

.potrfolio__grid {
  margin-bottom: 40px;
}

.breadcrumbs__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.breadcrumbs__item {
  display: block;
  border: 1px solid #ebebeb;
  padding: 0 25px;
  font-size: 14px;
  line-height: 32px;
  color: #8c8c8c;
  cursor: pointer;
}

@media (max-width: 776px) {
  .breadcrumbs__item {
    padding: 0 12px;
    margin-bottom: 10px;
  }
}

.breadcrumbs__item:hover, .breadcrumbs__item_active {
  border-radius: 3px;
  color: #fff;
  background-color: #998675;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 10px;
}

.grid-portfolio__inner {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
  margin: -15px -15px;
}

@media (max-width: 1200px) {
  .grid-portfolio__inner {
    grid-template-columns: auto auto;
  }
}

@media (max-width: 776px) {
  .grid-portfolio__inner {
    display: block;
  }
}

.grid-portfolio__item {
  padding: 15px 15px;
}

.grid-portfolio__icons {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  display: none;
}

.grid-portfolio__icons_show {
  display: flex;
  opacity: 1;
  transition: all 0.4s ease 0s;
}

.grid-portfolio__icon {
  width: 26px;
  height: 26px;
  fill: #fff;
  transition: all 0.5s ease 0s;
  cursor: pointer;
}

.grid-portfolio__icon:not(:last-child) {
  margin-right: 10px;
}

.grid-portfolio__icon:hover, .grid-portfolio__icon_active {
  fill: #c7b299;
}

.grid-portfolio__title {
  margin-bottom: 15px;
}

.grid-portfolio__title h5 {
  color: #d1d1d1;
}

.carts__item {
  display: flex;
  flex-direction: column;
  padding: 15px 15px;
  cursor: pointer;
}

.carts__item:hover .carts__image:after, .carts__item_active .carts__image:after {
  z-index: 2;
  border-bottom: 15px solid #362f2d;
}

.carts__item:hover .carts__image:before, .carts__item_active .carts__image:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.507);
  z-index: 2;
}

.carts__item:hover .carts__bottom, .carts__item_active .carts__bottom {
  background: #362f2d;
}

.carts__item:hover .carts__title h5, .carts__item_active .carts__title h5 {
  color: #fff;
}

.carts__item:hover .carts__subtitle, .carts__item_active .carts__subtitle {
  color: #c7b299;
}

.carts__item:hover .grid-portfolio__icons, .carts__item_active .grid-portfolio__icons {
  display: flex;
}

.carts__image {
  position: relative;
}

.carts__image:after {
  content: "";
  position: absolute;
  left: 20px;
  bottom: -2px;
  height: 10px;
  width: 30px;
  border: 20px solid transparent;
  border-bottom: 15px solid #fbfaf8;
}

.carts__img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 1200px) {
  .carts__img {
    width: 100%;
  }
}

@media (max-width: 776px) {
  .carts__img {
    object-fit: fill;
  }
}

.carts__bottom {
  padding: 24px 28px;
  background-color: #fbfaf8;
  text-align: left;
}

.carts__title h5 {
  font-family: "Raleway", sans-serif !important;
  font-weight: 500;
  font-size: 20px;
  color: #c7b299;
}

.carts__subtitle {
  font-size: 14px;
  color: #d1d1d1;
}

.fade {
  animation-name: fade;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.hide {
  display: none;
}

@keyframes fade {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

.modal {
  position: fixed;
  background: rgba(0, 0, 0, 0.404);
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: hidden;
  padding: 30px 10px;
}

.modal__inner {
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__close {
  position: absolute;
  top: 8px;
  right: 14px;
  font-size: 30px;
  color: #000;
  opacity: .5;
  font-weight: 700;
  border: none;
  background-color: transparent;
  cursor: pointer;
  z-index: 100;
}

.modal__close:hover {
  opacity: 1;
}

.modal__content {
  position: relative;
  background-color: #fff;
  padding: 30px;
  border-radius: 5px;
}

.modal__img {
  width: 100%;
}

.video__inner {
  position: relative;
}

.video__inner:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  background: rgba(17, 17, 17, 0.5);
}

.video__outher {
  max-height: 705px;
  width: 100%;
  object-fit: cover;
  min-height: 400px;
}

.video__play {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 2;
  border-radius: 100%;
  padding: 15px;
  background-color: rgba(17, 17, 17, 0.342);
  transform: translate(-50%, -141%);
}

.video__btn-play {
  width: 70px;
  height: 70px;
  fill: #fff;
  transition: transform 0.5s ease 0s;
  cursor: pointer;
}

.video__btn-play:hover {
  transform: scale(1.1);
}

@media (max-width: 992px) {
  .video__btn-play {
    width: 40px;
    height: 40px;
  }
}

.video__stop {
  content: "";
  position: absolute;
  left: 1%;
  bottom: 1%;
  transition: transform 0.5s ease 0s;
}

.video__stop:hover {
  transform: scale(1.1);
}

.video__btn-stop {
  width: 50px;
  height: 50px;
  fill: #fff;
}

.video__tracker {
  position: absolute;
  width: 90%;
  height: 30px;
  bottom: 1.5%;
  right: 3%;
  border-radius: 50%;
}

.video__content {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 3;
  transform: translate(-50%, -3%);
}

.video__title, .video__descr {
  max-width: 552px;
  color: #fff;
}

@media (max-width: 540px) {
  .video__descr {
    display: none;
  }
}

.video__timer {
  font-size: 14px;
  color: #c7b299;
}

.video_show {
  position: relative;
}

#progress {
  width: 100%;
  height: 10px;
}

.presentation {
  padding-top: 67px;
}

.presentation__top {
  display: flex;
  align-items: center;
  padding-left: 47px;
}

@media (max-width: 992px) {
  .presentation__top {
    padding: 0;
    flex-direction: column-reverse;
  }
}

.presentation__image {
  max-width: 423px;
  opacity: 0;
  transition: all 3s ease 0s;
}

.presentation__image img {
  max-width: 100%;
  object-fit: cover;
}

.presentation__image._anim-active {
  opacity: 1;
}

.presentation__content {
  text-align: left;
  flex: 1 1 auto;
  max-width: 660px;
  margin-top: -47px;
  transform: translateX(200%);
  transition: transform 1s ease 0s;
}

@media (max-width: 992px) {
  .presentation__content {
    margin-bottom: 20px;
  }
}

.presentation__content._anim-active {
  transform: translateX(0);
}

.presentation__title {
  margin-bottom: 30px;
}

.presentation__title h3 {
  color: #555;
}

.presentation__descr {
  margin-bottom: 33px;
}

@media (max-width: 776px) {
  .presentation__descr {
    margin: 15px;
  }
}

.presentation__descr p {
  color: #8c8c8c;
}

.presentation__list {
  margin-left: 57px;
}

@media (max-width: 776px) {
  .presentation__list {
    margin: 0;
  }
}

.presentation__item {
  line-height: 29px;
  color: #8c8c8c;
  background: url("../img/sprite.svg#arrow-right") left/21px no-repeat;
  padding-left: 40px;
}

.presentation__item:not(:last-child) {
  margin-bottom: 5px;
}

.presentation-counter {
  padding: 69px 0 55px;
  background: linear-gradient(to right, #211b19, #4e3427);
}

@media (max-width: 992px) {
  .presentation-counter {
    padding: 30px 0;
  }
}

.presentation-counter__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -34px;
}

.presentation-counter__item {
  position: relative;
  padding: 0 34px 27px;
  text-align: center;
}

@media (max-width: 992px) {
  .presentation-counter__item {
    padding: 15px 15px;
  }
}

.presentation-counter__item:after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 30px;
  height: 2px;
  background: #fff;
  transform: translateX(-50%);
}

.presentation-counter__icon {
  margin-bottom: 25px;
  height: 44px;
  max-width: 44px;
  fill: #c7b299;
}

.presentation-counter__count {
  margin-bottom: 15px;
  min-width: 130px;
  font-weight: 200;
  font-size: 48px;
  line-height: 0.75;
  text-transform: uppercase;
  color: #fff;
}

.presentation-counter__subtitle {
  font-size: 14px;
  line-height: 1.43;
  color: #c7b299;
}

.last-projects {
  padding: 80px 0 72px;
}

@media (max-width: 776px) {
  .last-projects {
    padding: 30px;
  }
}

.last-projects__inner {
  position: relative;
}

.last-projects__header {
  margin-bottom: 44px;
}

.last-projects__item {
  position: relative;
  margin: 0 auto;
  padding: 0;
  max-width: 370px;
}

.last-projects__image {
  height: 220px;
  max-width: 370px;
  position: relative;
}

.last-projects__image:after, .last-projects__image:before {
  display: none;
}

.last-projects__slide {
  width: 100%;
  height: 100%;
}

.last-projects__pagination {
  padding-bottom: 5px;
}

.last-projects__bottom {
  background: #f8f8f8;
}

.last-projects__title {
  margin-bottom: 19px;
}

.last-projects__title h5 {
  color: #555;
}

.last-projects__descr {
  margin-bottom: 27px;
}

.last-projects__descr p {
  line-height: 1.57;
  color: #8c8c8c;
}

.last-projects__link {
  color: #c7b299;
}

.last-projects__outher-slider {
  margin-bottom: 50px;
}

.last-projects__outher-controls {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.last-projects__button-next {
  right: 0;
}

.last-projects__button-prev {
  left: 0;
}

.last-projects__button-next, .last-projects__button-prev {
  position: relative;
  opacity: 0.4;
  transition: opacity 0.8s ease 0s;
}

.last-projects__button-next:after, .last-projects__button-prev:after {
  border: 2px solid #c7b299;
  border-radius: 100%;
  padding: 3px 6px;
  font-size: 15px !important;
  color: #c7b299;
}

.last-projects__button-next:hover, .last-projects__button-prev:hover {
  opacity: 1;
}

.last-projects__date {
  position: absolute;
  left: 20px;
  top: 19px;
  z-index: 5;
  text-align: center;
  color: #fff;
}

.last-projects__date_active .last-projects__day {
  background: #c7b299;
}

.last-projects__date_active .last-projects__month {
  background: #a48d72;
}

.last-projects__day {
  border-radius: 3px 3px 0 0;
  padding: 10px;
  font-size: 24px;
  line-height: 0.83;
  text-align: center;
  background-color: #363636;
}

.last-projects__month {
  border-radius: 0 0 3px 3px;
  padding: 7px 13px;
  font-family: 'Roboto';
  font-size: 12px;
  background-color: #252525;
}

.link {
  position: relative;
  padding-right: 20px;
  color: #c7b299;
}

.link:after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  border: 5px solid transparent;
  border-left: 5px solid #c7b299;
  transform: translateY(-46%);
}

.logos {
  position: relative;
  padding: 140px 0;
}

.logos:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.418);
  z-index: 2;
}

.logos__inner {
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 -45px;
  flex-wrap: wrap;
}

@media (max-width: 992px) {
  .logos__inner {
    justify-content: center;
    margin: 0;
  }
}

.logo__item {
  height: 210px;
  display: flex;
  align-items: center;
  padding: 0px 45px;
  cursor: pointer;
  transition: transform 0.4s ease 0s;
}

@media (max-width: 992px) {
  .logo__item {
    padding: 20px;
  }
}

.logo__item:hover {
  transform: scale(1.1);
}

.logo__item img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.conclusion {
  padding: 80px 0 81px;
  color: #555;
  background-color: #191919;
}

@media (max-width: 776px) {
  .conclusion {
    padding: 20px 0;
  }
}

.conclusion__inner {
  display: flex;
  justify-content: space-between;
  margin: 0 -10px;
}

@media (max-width: 776px) {
  .conclusion__inner {
    flex-direction: column;
  }
}

.conclusion__column {
  flex: 0 0 25%;
  padding: 0 10px;
}

.conclusion__column:not(:first-child) {
  padding-top: 25px;
}

.conclusion__logo {
  margin-bottom: 22px;
}

.conclusion__descr {
  margin-bottom: 28px;
  line-height: 1.50;
}

.conclusion__link {
  color: #fff;
}

.conclusion__link:after {
  border-left-color: #fff;
}

.conclusion__title {
  margin-bottom: 30px;
}

.conclusion__title h4 {
  font-family: "Raleway", sans-serif !important;
  font-weight: bold;
  font-size: 24px;
  color: #fff;
}

.conclusion__galery {
  display: flex;
  flex-wrap: wrap;
  margin: -5px;
}

.conclusion__image {
  padding: 5px 5px;
}

.post__item {
  margin-bottom: 10px;
  cursor: pointer;
}

.post__item:not(:last-child) {
  border-bottom: 1px solid #252525;
}

.post__item_active .post__text p, .post__item:hover .post__text p {
  color: #c7b299;
}

.post__item_active .post__date,
.post__item_active .post__time-ago, .post__item:hover .post__date,
.post__item:hover .post__time-ago {
  color: #fff;
}

.post__date, .post__time-ago {
  margin-bottom: 10px;
  font-size: 12px;
  color: #363636;
}

.post__text {
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 1.4;
}

.post__text span {
  color: #00bff3;
}

.footer {
  background-color: #000;
  padding: 45px 0;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 776px) {
  .footer__inner {
    flex-direction: column;
  }
}

.footer__row {
  display: flex;
}

.footer__item {
  position: relative;
}

.footer__item:not(:last-child) {
  margin-right: 25px;
}

.footer__item:not(:last-child):after {
  content: "";
  position: absolute;
  right: -10px;
  top: 0;
  height: 100%;
  width: 1px;
  background: #555;
}

@media (max-width: 776px) {
  .footer__item:not(:last-child) {
    margin-bottom: 15px;
  }
}

.footer__link {
  color: #555;
  font-size: 13px;
  transition: all 0.8s ease 0s;
}

.footer__link_active, .footer__link:hover {
  color: #c7b299;
}

.footer__link span {
  color: #959595;
}

.burger {
  width: 40px;
  height: 20px;
  position: relative;
  color: #000;
  display: none;
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: transform 0.3s ease-in-out;
}

.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger::before {
  top: 0;
}

.burger::after {
  bottom: 0;
}

.burger_active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger_active::before {
  transform: rotate(45deg);
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger_active::after {
  transform: rotate(-45deg);
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

@media (max-width: 992px) {
  .burger {
    display: block;
    position: relative;
    z-index: 3;
  }
}
